@import url(https://fonts.googleapis.com/css?family=Alata|Muli&display=swap);
/* 'Muli', sans-serif; */
/* 'Alata', sans-serif; */


body {
    background-color: black;
    font-family: 'Muli', sans-serif;
}

.wrapper{
    height: 100vh; 
    display: flex; 
    background-color: black;
    justify-content: center;
    align-items: center;
}

.main {
    margin: 0 auto; 
    padding: 20px;
    height: 70vh; 
    background: #FFA25F; 
    border-radius: 15px;
    text-align: center;
}

.containerBox{
    justify-content: center;
    padding: 20px;
}

.form {
    padding-top: 20px;
}

input{
    background-color: #FFA25F;;
    border: 0; 
    border-bottom: solid 1px #F2F2F2;
    color: #F2F2F2; 
    letter-spacing: 2px;
    font-size: 18px;
    padding: 10px;
    outline: none;
}

button {
    border-radius: 20px; 
    padding: 10px;
    margin-left: 20px; 
    letter-spacing: 2px;
    font-size: 18px;
    background-color:#737373;
    border: none; 
    color:#F2F2F2;
}

button:focus{
    outline: none;
}

.titleNamn {
    color: #F2F2F2;
}

p {
    color: #F2F2F2; 
    font-size: 18px; 
    padding-top: 8px;
    letter-spacing: 2px;
}

h1 {
    font-size: 45px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

